import React from "react";
import { Link, useParams } from "react-router-dom";

const LibraryTabChnageLink = ({
  carryParentState,
  activeTab,
  customStyle,
  role,
  children,
}) => {
  const params = useParams();

  return (
    <>
      {role === "FULL_FUNCTIONALITY" ? (
        <Link
          to={`/q/${params.username}/member/library?a=${activeTab}`}
          state={{ ...carryParentState }}
          style={{ ...customStyle }}
        >
          {children}
        </Link>
      ) : role === "DEPARTMENT_WISE" ? (
        DEPARTMENTWISE?.includes(activeTab) ? (
          <Link
            to={`/q/${params.username}/member/library?a=${activeTab}`}
            state={{ ...carryParentState }}
            style={{ ...customStyle }}
          >
            {children}
          </Link>
        ) : (
          <div
            style={{
              opacity: "0.5",
            }}
          >
            {children}
          </div>
        )
      ) : role === "FINE_CHARGE" ? (
        FINE?.includes(activeTab) ? (
          <Link
            to={`/q/${params.username}/member/library?a=${activeTab}`}
            state={{ ...carryParentState }}
            style={{ ...customStyle }}
          >
            {children}
          </Link>
        ) : (
          <div
            style={{
              opacity: "0.5",
            }}
          >
            {children}
          </div>
        )
      ) : (
        <Link
          to={`/q/${params.username}/member/library?a=${activeTab}`}
          state={{ ...carryParentState }}
          style={{ ...customStyle }}
        >
          {children}
        </Link>
      )}
    </>
  );
};

export default LibraryTabChnageLink;

const FINE = ["book", "charge", "member"];
const DEPARTMENTWISE = [
  "book",
  "issue",
  "collect",
  "charge",
  "member",
  "export",
  "logs",
  "book&n=edit",
];
